import util from '@/common/util.js'
import graceChecker from '@/common/graceChecker.js'
export default {
	data() {
		return {
			userId: '',
		};
	},

	methods: {
		next() {
			let _this = this
			var codeRule = [
				{
					name: 'userId',
					checkType: 'notnull',
					errorMsg: '请输入账号'
				}
			];
			var checkRes = graceChecker.check(_this, codeRule);
			if (!checkRes) {
				uni.showToast({
					title: graceChecker.error,
					icon: "none"
				});
				_this.isFocus = true;
				return;
			}
			uni.request({
				url: this.Server_IP + 'mailfromUserId', //仅为示例，并非真实接口地址。
				data: {
					userId: this.userId
				},
				header: {
					'custom-header': 'mailfromUserId' //自定义请求头信息
				},
				method:"POST",
				dataType:"json",
				success: (res) => {
					console.log(res.data);
					if(res.data.info.code == '0'){
						uni.navigateTo({
						    url: './verification_mail?mail='+res.data.data.mail+'&userId='+this.userId
						});
					}else{
						uni.showToast({
							icon: 'none',
							title: res.data.info.message
						});
					}
				},
			});

		}
	}
}
